"use client";

import React from 'react';
import { Package, MapPin, Wrench } from 'lucide-react';
import { motion } from 'framer-motion';

export default function AboutPage() {
    const services = [
        {
            title: "1. สั่งซื้อออนไลน์",
            subtitle: "(ประหยัดที่สุด)",
            icon: <Package size={40} className="text-gray-700" />,
            description: "เลือกสินค้าและแคตตาล็อกออนไลน์ วัดพื้นที่เอง สั่งผลิต และติดตั้งเอง",
            details: [
                "เลือกประเภท/แคตตาล็อกออนไลน์",
                "วัดขนาดด้วยตัวเอง",
                "สั่งผลิตและส่งถึงบ้าน",
                "ติดตั้งเอง"
            ],
            price: "ราคาสินค้า + ค่าจัดส่ง",
            highlight: false
        },
        {
            title: "2. บริการวัดหน้างาน",
            subtitle: "(ติดตั้งเอง)",
            icon: <MapPin size={40} className="text-gray-700" />,
            description: "เรานำแคตตาล็อกไปให้เลือกถึงบ้าน พร้อมวัดพื้นที่ให้ แต่ลูกค้าติดตั้งเอง",
            details: [
                "เลือกประเภท/แคตตาล็อกเบื้องต้น",
                "นัดหมายวัดพื้นที่หน้างาน",
                "นำตัวอย่างไปให้เลือกจริง",
                "สั่งผลิตและส่งถึงบ้าน",
                "ติดตั้งเอง"
            ],
            price: "ค่าวัดหน้างาน + ราคาสินค้า + ค่าจัดส่ง",
            highlight: false
        },
        {
            title: "3. บริการครบวงจร",
            subtitle: "(แนะนำ)",
            icon: <Wrench size={40} className="text-yellow-600" />,
            description: "ดูแลให้หมด ตั้งแต่เลือกแบบ วัดพื้นที่ ผลิต จนถึงติดตั้งพร้อมรับประกัน",
            details: [
                "เลือกประเภท/แคตตาล็อกเบื้องต้น",
                "นัดหมายวัดพื้นที่หน้างาน",
                "นำตัวอย่างไปให้เลือกจริง",
                "ทีมช่างมืออาชีพติดตั้งให้",
                "รับประกันงานติดตั้ง"
            ],
            price: "ค่าวัดหน้างาน + ราคาสินค้า + ค่าติดตั้ง",
            highlight: true
        }
    ];

    return (
        <div style={{ maxWidth: '1000px', margin: '0 auto', padding: '3rem 1rem' }}>
            <div style={{ textAlign: 'center', marginBottom: '4rem' }}>
                <h1 style={{ fontSize: '2.5rem', fontWeight: 700, marginBottom: '1rem' }}>เกี่ยวกับเรา</h1>
                <p style={{ fontSize: '1.2rem', color: '#333', maxWidth: '600px', margin: '0 auto' }}>
                    เราคือ <strong>SAMREDROOB</strong> มุ่งมั่นที่จะทำให้บ้านของคุณสวยงามน่าอยู่
                    ด้วยบริการผ้าม่านและวอลเปเปอร์คุณภาพครบวงจร
                </p>
            </div>

            <h2 style={{ fontSize: '1.8rem', fontWeight: 700, marginBottom: '2rem', textAlign: 'center' }}>
                รูปแบบการให้บริการของเรา
            </h2>

            <div style={{
                display: 'grid',
                gridTemplateColumns: 'repeat(auto-fit, minmax(300px, 1fr))',
                gap: '2rem'
            }}>
                {services.map((service, index) => (
                    <motion.div
                        key={index}
                        initial={{ opacity: 0, y: 20 }}
                        animate={{ opacity: 1, y: 0 }}
                        transition={{ delay: index * 0.1 }}
                        style={{
                            backgroundColor: service.highlight ? 'white' : 'white',
                            border: service.highlight ? '2px solid var(--color-accent)' : '1px solid #eee',
                            borderRadius: 'var(--radius-lg)',
                            padding: '2rem',
                            boxShadow: service.highlight ? 'var(--shadow-lg)' : 'var(--shadow-card)',
                            position: 'relative',
                            display: 'flex',
                            flexDirection: 'column'
                        }}
                    >
                        {service.highlight && (
                            <span style={{
                                position: 'absolute',
                                top: '-12px',
                                right: '20px',
                                backgroundColor: 'var(--color-accent)',
                                color: 'white',
                                padding: '4px 12px',
                                borderRadius: '20px',
                                fontSize: '0.8rem',
                                fontWeight: 700
                            }}>
                                แนะนำ
                            </span>
                        )}

                        <div style={{ marginBottom: '1.5rem', display: 'flex', justifyContent: 'center' }}>
                            {service.icon}
                        </div>

                        <h3 style={{ fontSize: '1.25rem', fontWeight: 700, marginBottom: '1rem', textAlign: 'center' }}>
                            {service.title} <br />
                            <span style={{ fontSize: '1rem', fontWeight: 500, color: '#555' }}>
                                {service.subtitle}
                            </span>
                        </h3>

                        <p style={{ color: '#333', marginBottom: '1.5rem', textAlign: 'center', minHeight: '3rem', fontWeight: 500 }}>
                            {service.description}
                        </p>

                        <div style={{
                            backgroundColor: '#f8f9fa',
                            borderRadius: 'var(--radius-md)',
                            padding: '1rem',
                            marginBottom: '1.5rem',
                            flex: 1
                        }}>
                            <ul style={{ paddingLeft: '1.2rem', margin: 0 }}>
                                {service.details.map((detail, idx) => (
                                    <li key={idx} style={{ marginBottom: '0.5rem', fontSize: '1rem', color: '#000', fontWeight: 500 }}>
                                        {detail}
                                    </li>
                                ))}
                            </ul>
                        </div>

                        <div style={{
                            textAlign: 'center',
                            paddingTop: '1rem',
                            borderTop: '1px solid #eee',
                            fontWeight: 700,
                            color: service.highlight ? 'var(--color-accent)' : 'var(--color-primary)',
                            whiteSpace: 'nowrap',
                            fontSize: '0.9rem'
                        }}>
                            {service.price}
                        </div>
                    </motion.div>
                ))}
            </div>
        </div>
    );
}
